.App {
  /* background-color: #0d102d; */
  width: 100%;
  overflow: hidden;
}

.App .one {
  /* background-color: #0d102d; */
  color: black;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.App .model {
  margin-left: 5em;
  overflow: hidden;
  margin-top: 5em;
  height: 100vh;
  width: 120%;
}
.App .landingBody {
  width: 84%;
  position: absolute;
  height: 100vh;
  top: 0;
  margin-left: 8%;
  z-index: 332;
}
.App .body {
  width: 100%;
}
@media (max-width: 700px) {
  .App .landingBody {
    width: 90%;
    margin-left: 5%;
  }
  .App .model {
    margin-left: -21em;
    margin-top: -3em;
    height: 140vh;
    width: 240%;
    scale: 0.6;
  }
}
